@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.profile-container {
    display: flex;
    height: 100vh;
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    font-family: 'Comfortaa', sans-serif;
}

.sidebar {
    width: 220px;
    padding: 40px 20px;
    color: #8e61a6;
    font-weight: 600;
}

.sidebar h2 {
    font-size: 24px;
    margin-bottom: 30px;
    font-family: "Aboreto", system-ui;
}

.sidebar nav a {
    display: block;
    margin-bottom: 20px;
    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

.sidebar nav .active,
.sidebar nav a:hover {
    color: #8e61a6;
    font-weight: bold;
    text-decoration: underline;
}

.profile-main {
    flex: 1;
    background: white;
    margin: 20px;
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
}

.profile-main h2 {
    font-size: 24px;
    margin-bottom: 5px;
}

.profile-main p {
    margin-bottom: 30px;
    color: #666;
}

.profile-card {
    display: flex;
    align-items: center;
    gap: 40px;
    margin-bottom: 30px;
}

.avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: url('../assets/avatar.jpg')
}

.profile-info p {
    font-size: 18px;
    margin: 8px 0;
    color: #8e61a6;
}

.logout-button-user {
    padding: 12px 30px;
    background-color: #a183b1;
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 16px;
    cursor: pointer;
    width: 300px;
}