@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.admin-dashboard-container {
    display: flex;
    height: 100vh;
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    font-family: 'Comfortaa', sans-serif;
}

.admin-sidebar {
    width: 220px;
    padding: 40px 20px;
    align-items: center;
    color: #8e61a6;
    font-weight: 600;
}

.admin-sidebar h2 {
    font-size: 24px;
    margin-bottom: 30px;
    font-family: "Aboreto", system-ui;
}

.admin-sidebar nav a {
    display: block;
    margin-bottom: 20px;
    color: #a092af;
    text-decoration: none;
    cursor: pointer;
}

.admin-sidebar nav .active,
.admin-sidebar nav a:hover {
    color: #8e61a6;
    font-weight: bold;
    text-decoration: underline;
}

.logout-button {
    padding: 10px 25px;
    background-color: #a183b1;
    width: 200px;
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 14px;
    margin-top: 550px;
    cursor: pointer;
}

.admin-main {
    flex: 1;
    background: white;
    margin: 20px;
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
}

.admin-main h2 {
    font-size: 24px;
    margin-bottom: 5px;
}

.admin-main p {
    margin-bottom: 30px;
    color: #666;
}

.admin-accounts {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.admin-account-card {
    background: url('../assets/card-bg.jpg') no-repeat center center / cover;
    padding: 20px;
    border-radius: 25px;
    width: 250px;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
    color: #8e61a6;
}

.admin-account-card h4 {
    font-weight: bold;
    margin-bottom: 5px;
}

.admin-account-card p {
    margin: 10px 0;
}

.admin-account-card button {
    margin-top: 15px;
    margin-right: 15px;
    padding: 8px 20px;
    background-color: #8e61a6;
    color: white;
    border: none;
    border-radius: 25px;
    cursor: pointer;
}