@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.transfer-container {
    display: flex;
    height: 100vh;
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    font-family: 'Comfortaa', sans-serif;
}

.sidebar {
    width: 220px;
    padding: 40px 20px;
    color: #8e61a6;
    font-weight: 600;
}

.sidebar h2 {
    font-size: 24px;
    margin-bottom: 30px;
    font-family: "Aboreto", system-ui;
}

.sidebar nav a {
    display: block;
    margin-bottom: 20px;
    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

.sidebar nav .active,
.sidebar nav a:hover {
    color: #8e61a6;
    font-weight: bold;
    text-decoration: underline;
}

.transfer-main {
    flex: 1;
    background: white;
    margin: 20px;
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
}

.transfer-main h2 {
    font-size: 24px;
    margin-bottom: 5px;
}

.transfer-main p {
    margin-bottom: 30px;
    color: #666;
}

.transfer-form {
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    border-radius: 30px;
    padding: 30px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.input-row {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
}

.input-group {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.half-width {
    width: 50%;
    margin-bottom: 20px;
}

.input-group label {
    margin-bottom: 5px;
    color: #8e61a6;
}

.input-group input {
    padding: 12px;
    border: none;
    border-radius: 20px;
    outline: none;
}

.confirm-button {
    width: 100%;
    padding: 12px;
    background-color: #a183b1;
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 16px;
    cursor: pointer;
}


