.auth-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    font-family: 'Be Vietnam', sans-serif;
}

.auth-box {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 40px 30px;
    width: 320px;
    text-align: center;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.auth-title {
    font-size: 56px;
    font-weight: 700;
    color: #8e61a6;
    margin-bottom: 5px;
    font-family: "Aboreto", system-ui;
}

.auth-subtitle {
    font-size: 12px;
    color: #8e61a6;
    margin-bottom: 50px;
    letter-spacing: 1px;
    font-family: "Aboreto", system-ui;
}

.auth-buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
    gap: 10px;
}

.auth-buttons button {
    flex: 1;
    border: none;
    padding: 10px;
    font-weight: 600;
    font-size: 12px;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s;
}

.auth-buttons .active {
    background-color: #a283c7;
    color: white;
}

.auth-buttons .active:hover {
    background-color: #8b63b1;
}

.auth-buttons .inactive {
    background-color: white;
    color: #a283c7;
}

.auth-input {
    width: 300px;
    padding: 12px;
    margin: 10px 0;
    border-radius: 30px;
    border: none;
    font-size: 14px;
    outline: none;
    text-align: left;
}

.auth-submit {
    background-color: #a283c7;
    color: white;
    font-weight: bold;
    padding: 12px;
    width: 300px;
    border: none;
    border-radius: 30px;
    margin-top: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.auth-submit:hover {
    background-color: #8b63b1;
}