@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.dashboard-container {
    display: flex;
    height: 100vh;
    background: url('../assets/bg.jpg') no-repeat center center / cover;
    font-family: 'Comfortaa', sans-serif;
}

.sidebar {
    width: 220px;
    background: transparent;
    align-items: center;
    padding: 40px 20px;
    color: #7d4e97;
    font-weight: 600;
}

.sidebar h2 {
    font-size: 24px;
    margin-bottom: 30px;
    font-family: "Aboreto", system-ui;
}

.sidebar nav a {
    display: block;
    margin-bottom: 20px;
    color: #000000;
    text-decoration: none;
    cursor: pointer;
    transition: color 0.3s;
}

.sidebar nav .active,
.sidebar nav a:hover {
    color: #8e61a6;
    font-weight: bold;
    text-decoration: underline;
}

.main-content {
    flex: 1;
    background: white;
    margin: 20px;
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
}

.main-content h1 {
    font-size: 28px;
    color: #444;
}

.main-content p {
    font-size: 14px;
    color: #444;
}

.main-content h3 {
    margin-top: 30px;
    margin-bottom: 10px;
    color: #333;
}

.accounts {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.account-card {
    width: 250px;
    height: 130px;
    background: url('../assets/card-bg.jpg') no-repeat center center / cover;
    border-radius: 20px;
    padding: 15px;
    color: #333;
    position: relative;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
}

.account-card .balance {
    font-weight: bold;
    font-size: 16px;
}

.account-card .number {
    font-size: 12px;
    margin-top: 5px;
}

.account-card .status {
    margin-bottom: 25px;
    color: #666;
}

.account-card button {
    position: absolute;
    bottom: 25px;
    right: 25px;
    padding: 6px 12px;
    border: none;
    border-radius: 20px;
    background-color: #8e61a6;
    color: white;
    cursor: pointer;
}

.transactions-table {
    width: 100%;
    margin-top: 20px;
    border-collapse: collapse;
}

.transactions-table th,
.transactions-table td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #eee;
}
